.layout {
  background-color: rgb(255, 255, 255);
}
.layout .header {
  display: flex;
  justify-content: space-between;
}
.layout .header .headerleft {
  width: 40%;
}
.layout .header .headerright {
  width: 5%;
  background-color: rgba(240, 248, 255, 0.74);
  top:0%;
  right: 0%;
}
.layout .img {
  width: 100%;
  height: 400px;
}
.layout .footer{
  background-color: rgb(205, 202, 202);
  width: 100%;
  height: 50px;
  position: absolute;
  bottom: 0;
}
p{
  color: #9a9696;
  text-align: center;
}
